<template>
  <div class="totop-container" @click="hanldeClick" v-show="show">ToTop</div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    this.$bus.$on("mainScroll", this.hanldeMainScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.hanldeMainScroll);
  },
  methods: {
    hanldeMainScroll(dom) {
      this.show = dom.scrollTop >= 1000;
    },
    hanldeClick() {
      this.$bus.$emit("setMainScroll", 0);
    },
  },
};
</script>

<style scoped lang="less">
@number: 70px;
.totop-container {
  position: fixed;
  right: @number;
  bottom: @number;
  width: @number;
  height: @number;
  border-radius: 50%;
  color: #fff;
  line-height: @number;
  text-align: center;
  cursor: pointer;
  transition: 0.4s;
  z-index: 999;
  background: rgb(70, 107, 187);
  &:hover {
    transform: scale(1.4);
  }
}
</style>
