export default function (refValue) {
    return {
        mounted() {
            this.$bus.$on("setMainScroll", this.setMainScroll);
            this.$refs[refValue].addEventListener("scroll", this.hanldeScroll);
        },

        beforeDestroy() {
            this.$refs[refValue].removeEventListener("scroll", this.hanldeScroll);
            this.$bus.$off("setMainScroll", this.setMainScroll);
        },
        methods: {
            hanldeScroll() {
                this.$bus.$emit("mainScroll", this.$refs[refValue]);
            },
            setMainScroll(scrollTop) {
                this.$refs[refValue].scrollTop = scrollTop;
            },
        }
    }
}