<template>
  <div class="message-container" ref="messageContainer">
    <MessageArea
      title="评论列表"
      :subTitle="data.total"
      :isListLoading="isLoading"
      :list="data.rows"
      @hanldeSubmit="hanldeSubmit"
    />
    <p
      style="
        text-align: center;
        color: #03a9f4a6;
        font-size: 20px;
        margin-top: -80px;
      "
      v-if="this.data.rows && !this.isOver"
    >
      加载完成
    </p>
    <ToTop />
  </div>
</template>

<script>
import ToTop from "@/components/ToTop";
import setMainScroll from "@/mixins/setMainScroll";
import MessageArea from "@/components/MessageArea/";
import fetchData from "@/mixins/fetchData";
import * as useApi from "@/api/blog";
export default {
  mixins: [fetchData({}), setMainScroll("messageContainer")],
  components: {
    MessageArea,
    ToTop,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  computed: {
    isOver() {
      return this.data.rows.length < this.data.total;
    },
  },
  mounted() {
    this.$bus.$on("mainScroll", this.mainScroll);
  },
  methods: {
    mainScroll(dom) {
      const range = 100;
      const exact = Math.abs(
        dom.clientHeight + dom.scrollTop - dom.scrollHeight
      );
      if (!this.isLoading) {
        if (range > exact) {
          this.fetchMore();
        }
      }
    },
    async fetchMore() {
      if (!this.isOver) {
        return;
      }
      this.isLoading = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
    async fetchData() {
      const resp = await useApi.getComment(this.page, this.limit);
      return resp.data;
    },
    async hanldeSubmit(data, callback) {
      const resp = await useApi.postComment(data);
      setTimeout(() => {
        callback("评论成功!");
        this.data.rows.unshift(resp.data);
        this.data.total++;
        return resp.data;
      }, 1000);
    },
  },
};
</script>

<style scoped>
.message-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.messageArea-container {
  width: 90%;
  margin: 0 auto;
}
</style>
