<template>
  <form
    class="message-form-container"
    @submit.prevent="hanldeSubmit"
    ref="container"
  >
    <div class="input">
      <input
        class="text"
        type="text"
        maxlength="10"
        placeholder="用户昵称"
        v-model="formData.nickname"
      />
      <span>{{ formData.nickname.length }}/10</span>
    </div>
    <div class="error">{{ error.nickname }}</div>
    <div class="textarea">
      <textarea
        placeholder="内容"
        maxlength="300"
        class="area"
        v-model="formData.content"
      >
      </textarea>
      <span>{{ formData.content.length }}/300</span>
    </div>
    <div class="error">{{ error.content }}</div>
    <div class="button">
      <button :disabled="isSubmiting" class="disabled">
        {{ isSubmiting ? "提交中..." : "提交" }}
      </button>
    </div>
  </form>
</template>

<script>
import { showMessage } from "@/utils/";
export default {
  data() {
    return {
      formData: {
        nickname: "",
        content: "",
      },
      error: {
        nickname: "",
        content: "",
      },
      isSubmiting: false,
    };
  },
  methods: {
    hanldeSubmit() {
      this.error.nickname = this.formData.nickname ? "" : "请输入账号";
      this.error.content = this.formData.content ? "" : "请输入内容";
      if (this.error.nickname || this.error.content) {
        return;
      }
      this.isSubmiting = true;
      this.$emit("hanldeSubmit", this.formData, (messageText) => {
        showMessage({
          content: messageText,
          container: this.$refs.container,
          type: "success",
        });
        this.isSubmiting = false;
        this.formData.nickname = "";
        this.formData.content = "";
      });
    },
  },
};
</script>

<style scoped lang="less">
.message-form-container {
  position: relative;
  margin-top: 40px;
  .input {
    width: 450px;
    height: 45px;
    position: relative;
  }
  .textarea {
    width: 90%;
    height: 300px;
    position: relative;
  }
  .error {
    height: 40px;
    line-height: 40px;
    color: rgb(255, 157, 0);
  }

  .text,
  .area {
    width: 100%;
    height: 100%;
    outline: none;
    border-radius: 10px;
    border: 1px dotted rgb(95, 169, 212);
    resize: none;
    &:focus {
      border-color: rgb(17, 0, 255);
    }
  }
  span {
    font-size: 12px;
    position: absolute;
    right: 0;
    bottom: 0;
    color: rgb(211, 201, 201);
  }
  .button {
    button {
      padding: 15px 40px;
      border-radius: 5px;
      font-size: 16px;
      background: rgb(33, 105, 187);
      color: #fff;
      cursor: pointer;
      outline: none;
      border: none;
      transition: 0.4s;
    }
    :disabled {
      background: rgb(101, 158, 101);
      color: rgb(255, 255, 255);
      cursor: not-allowed;
    }
  }
}
</style>
